import React, { useState, useRef, useEffect } from "react";
import css from "./style.module.css";
import { db } from "../../firebase";
import Button from "../General/Button";
import TextBox from "../General/TextBox";
import SelectComponent from "../General/SelectComponent";

const PatientRegistrationForm = () => {
    const [patientInfo, setPatientInfo] = useState({
        citizenship: "",
        fname: "",
        lname: "",
        gender: "",
        regNo: "",
        phoneNo: "",
        email: "",
        city: "",
        district: "",
        khoroo: "",
        building: ""
    });

    const [formCheck, setFormCheck] = useState({
        // errorMark: { required    true !     submit     
            citizenship: true,
            fname: true,
            lname: true,
            gender: true,
            regNo: true,
            phoneNo: true,
            email: "",
            city: true,
            district: true,
            khoroo: true,
            building: ""
        // },
    });

    const [formErrors, setFormErrors] = useState({ lname: "", fname: "" });
    //const [buttonText, setButtonText] = useState("");
    
    /*const submitForm = e => {
        e.preventDefault();

        if(buttonText === "") {
            alert("  .   !");
        } else if (buttonText === "") {
            alert("case 2");
        }
        setButtonText("");
    };*/

    const submitForm = e => {
        // console.log("checking the button to register patient", patientCitizenship, patientLname, patientFname, patientRegno, patientPhoneNo, patientCity, patientDistrict, patientKhoroo, patientBuilding);
        console.log("checking the button to register patient", patientInfo);
        console.log("checking submit form marks", formCheck);
        let textLeftWarningForSubmit = "";
        let textRightWarningForSubmit = "";


        if(formCheck.citizenship === true)
            textLeftWarningForSubmit += ` {}`;
        if(formCheck.lname === true)
            textLeftWarningForSubmit += ` {}`;
        if(formCheck.fname === true)
            textLeftWarningForSubmit += ` {}`;
        if(formCheck.gender === true)
            textLeftWarningForSubmit += ` {}`;
        if(formCheck.regNo === true)
            textLeftWarningForSubmit += ` {}`;
        if(formCheck.phoneNo === true)
            textLeftWarningForSubmit += ` {}`;
        if(formCheck.city === true)
            textLeftWarningForSubmit += ` {, }`;
        if(formCheck.district === true)
            textLeftWarningForSubmit += ` {, }`;
        if(formCheck.khoroo === true)
            textLeftWarningForSubmit += ` { , }`;

        textRightWarningForSubmit += "  ,    !";
    
        // var patientId = db.collection("patients").doc().;

        if(!formCheck.citizenship && !formCheck.lname && 
            !formCheck.fname && !formCheck.gender && 
            !formCheck.regNo && !formCheck.phoneNo && 
            !formCheck.city && !formCheck.district && !formCheck.khoroo) {
            //    
            var patientDocRef = db.collection("patients").doc();
            patientDocRef.set({
                patientId: patientDocRef.id,
                citizenship: patientInfo.citizenship,
                fname: patientInfo.fname,
                lname: patientInfo.lname,
                gender: patientInfo.gender,
                regNo: patientInfo.regNo,
                phoneNo: patientInfo.phoneNo,
                email: patientInfo.email,
                city: patientInfo.city,
                district: patientInfo.district,
                khoroo: patientInfo.khoroo,
                building: patientInfo.building
            }).then(() => {
                alert("  !");
            })
            .catch((error) => {
                console.log("  !");
            });
        } else {
            alert(textLeftWarningForSubmit + textRightWarningForSubmit);
        }

        textLeftWarningForSubmit = "";
    };

    const changeGender = e => {
        setPatientInfo({ ...patientInfo, gender: e.value });
    };

    const changeCitizenship = e => {
        setPatientInfo({ ...patientInfo, citizenship: e.value });
    };

    const changeCity = e => {
        setPatientInfo({ ...patientInfo, city: e.value });
    };

    const changeDistrict = e => {
        setPatientInfo({ ...patientInfo, district: e.value });
    };

    const optionsCity = [
        {value: "ulaanbaatar", label: "", componentLabel: "city"},
        {value: "arkhangai", label: "", componentLabel: "city"},
        {value: "bayan-ulgii", label: "-", componentLabel: "city"},
        {value: "bayankhongor", label: "", componentLabel: "city"},
        {value: "bulgan", label: "", componentLabel: "city"},
        {value: "govi-altai", label: "-", componentLabel: "city"},
        {value: "darkhan-uul", label: "-", componentLabel: "city"},
        {value: "dornogovi", label: "", componentLabel: "city"},
        {value: "dornod", label: "", componentLabel: "city"},
        {value: "dundgovi", label: "", componentLabel: "city"},
        {value: "zavkhan", label: "", componentLabel: "city"},
        {value: "orkhon", label: "", componentLabel: "city"},
        {value: "uvurkhangai", label: "", componentLabel: "city"},
        {value: "umnugovi", label: "", componentLabel: "city"},
        {value: "sukhbaatar", label: "", componentLabel: "city"},
        {value: "selenge", label: "", componentLabel: "city"},
        {value: "tuv", label: "", componentLabel: "city"},
        {value: "uvs", label: "", componentLabel: "city"},
        {value: "khovd", label: "", componentLabel: "city"},
        {value: "khuvsgul", label: "", componentLabel: "city"},
        {value: "khentii", label: "", componentLabel: "city"}
    ];

    const optionsDistrict = [
        {value: "baganuur", label: "", city: "ulaanbaatar", componentLabel: "district"},
        {value: "bagakhangai", label: "", city: "ulaanbaatar", componentLabel: "district"},
        {value: "bayangol", label: "", city: "ulaanbaatar", componentLabel: "district"},
        {value: "bayanzurkh", label: "", city: "ulaanbaatar", componentLabel: "district"},
        {value: "nalaikh", label: "", city: "ulaanbaatar", componentLabel: "district"},
        {value: "songinokhairkhan", label: "", city: "ulaanbaatar", componentLabel: "district"},
        {value: "sukhbaatar", label: "", city: "ulaanbaatar", componentLabel: "district"},
        {value: "khan-uul", label: "-", city: "ulaanbaatar", componentLabel: "district"},
        {value: "chingeltei", label: "", city: "ulaanbaatar", componentLabel: "district"}
    ];

    const optionsGender = [
        {value: "MALE", label: "", componentLabel: "patientGender"},
        {value: "FEMALE", label: "", componentLabel: "patientGender"}
    ];

    const optionsCitizenship = [
        {value: "MGL", label: "", componentLabel: "patientCitizenship"},
        {value: "ITL", label: "", componentLabel: "patientCitizenship"}
    ];

    // const filteredOptionsDistrict = optionsDistrict.filter(el => el.city === selectCity);
    
    return (
        <>
            {/* <input placeholder="haha...." ref={inputRef} /> */}
            <br/><br/>
            <SelectComponent 
                onchangefunc={(e) => changeCitizenship(e)} 
                options={optionsCitizenship} 
                label=" *"
                setComponentInfo={setPatientInfo}
                componentAllInfo={patientInfo}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />

            <TextBox 
                label="*" 
                name="lName" 
                setComponentInfo={setPatientInfo} 
                componentInfo={patientInfo['lname']} 
                componentAllInfo={patientInfo} 
                setWarning={setFormErrors} 
                warning={formErrors['lname']} 
                allwarnings={formErrors}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />

            <TextBox 
                label="*" 
                name="fName" 
                setComponentInfo={setPatientInfo} 
                componentInfo={patientInfo['fname']} 
                componentAllInfo={patientInfo} 
                setWarning={setFormErrors} 
                warning={formErrors['fname']} 
                allwarnings={formErrors}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />

            <SelectComponent 
                onchangefunc={(e) => changeGender(e)} 
                options={optionsGender} 
                label="*"
                setComponentInfo={setPatientInfo}
                componentAllInfo={patientInfo}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />

            <TextBox 
                name="regNo"
                label=" *" 
                setComponentInfo={setPatientInfo}
                componentInfo={patientInfo['regNo']}
                componentAllInfo={patientInfo}
                setWarning={setFormErrors}
                warning={formErrors['regNo']}
                allwarnings={formErrors}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />

            <TextBox 
                name="phoneNo"
                label="*" 
                setComponentInfo={setPatientInfo}
                componentInfo={patientInfo['phoneNo']}
                componentAllInfo={patientInfo}
                setWarning={setFormErrors}
                warning={formErrors['phoneNo']}
                allwarnings={formErrors}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />
            
            <TextBox 
                name="email"
                label="Email" 
                setComponentInfo={setPatientInfo}
                componentInfo={patientInfo['email']}
                componentAllInfo={patientInfo}
                setWarning={setFormErrors}
                warning={formErrors['email']}
                allwarnings={formErrors}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />
            
            <SelectComponent 
                onchangefunc={(e) => changeCity(e)} 
                options={optionsCity} label=", "
                setComponentInfo={setPatientInfo}
                componentAllInfo={patientInfo}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />
            
            <SelectComponent 
                onchangefunc={(e) => changeDistrict(e)} 
                options={optionsDistrict} 
                label=", "
                setComponentInfo={setPatientInfo}
                componentAllInfo={patientInfo}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />
            
            <TextBox 
                name="khoroo"
                label=", "
                setComponentInfo={setPatientInfo}
                componentInfo={patientInfo['khoroo']}
                componentAllInfo={patientInfo}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck} />
            
            <TextBox  
                name="building"
                label=", , "
                setComponentInfo={setPatientInfo}
                componentInfo={patientInfo['building']}
                componentAllInfo={patientInfo}
                setFormCheck={setFormCheck}
                formCheckInfo={formCheck}/>
            <br />
            <br />
            <Button text="" clicked={submitForm} />
        </>
    );
};

export default PatientRegistrationForm;